import Yox from 'yox'

import { UNDEFINED } from '../constant'

import LoadingBar from './LoadingBar'

interface Config {
  type?: string,
  color?: string,
  height?: number,
  percent?: number
}

let instance = UNDEFINED

function add(props: Config | void) {

  const wrapper = Yox.dom.find('#bell-loadingbar-wrapper') as HTMLElement

  instance = new Yox(
    Yox.object.extend(
      {
        el: wrapper,
        props,
      },
      LoadingBar
    )
  )

  return instance

}

function remove() {
  if (instance) {
    instance.destroy()
    instance = UNDEFINED
  }
}

(Yox.prototype as any).$loadingBar = {
  //  0 
  start(options?: Config) {
    if (instance) {
      remove()
    }
    return add(options)
  },
  // 
  finish() {
    if (instance) {
      instance.set('percent', 100)
      setTimeout(remove, 300)
    }
  },
  // 
  update(data: Config) {
    if (instance) {
      instance.set(data)
    }
  }
}


